.all{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.que{
    background-color: rgb(43, 25, 240);
    align-items: center;
    justify-content: center;
    display: flex;
    height: 20%;
    font-size: 40px;
    font-weight: 900;
    width: 80%;
    margin: 10px;
    border-radius: 10px;
    border: 1px solid white;
}
.answers{
    
    display: flex;
    align-items: center;
    height: 50%;
    width: 90%;
    flex-wrap: wrap;
    justify-content: center;
}
.answer{

    background-color:rgb(66, 50, 156);
    border-radius: 20px;
    align-items: center;
    border: 1px solid white;
    justify-content: center;
    width: 40%;
    padding: 8px;
    margin:0 10px 0 10px;
    font-size: 20px;
    font-weight: 500;
  
    display: flex;
    height: 18%;
    cursor: pointer;
}
.answer:hover,.answer.active{
    background-color: mediumblue;
    padding: 6px;
}
.answer.correct{
    animation: correct 3s forwards;
}
@keyframes correct {
    0%,20%,50%{
        background-color: green;
    }
    22%,52%,60%{
        background-color: transparent;
    }
    80%,100%{
        background-color: green;
    }
}
.answer.wrong{
    animation: wrong 3s forwards;
}
@keyframes wrong {
    0%,20%,50%,60%{
        background-color:crimson;
    }
    22%,52%,62%{
        background-color: transparent;
    }
    80%,100%{
        background-color:crimson;
    }
}