.start{
    height: 100vh;
    width: 100%;
    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.btn{
    display:flex;

    height: 50px;
    width: 300px;
    margin :5px;
    
    border-radius: 10px;
    
    color: black;
    font-size: 30px;
    align-items: center;
    text-align: center;
    justify-content: center;
    cursor: pointer;
    font-weight: bolder;

}
.name{
    margin: 5px;
    width: 300px;
    height: 30px;
    border: 2px solid black;
    border-radius: 10px;
    padding: 15px;
    color: black;
    font-size: 30px;
    align-items: center;
    text-align: center;
    justify-content: center;
    font-weight: 900;
    cursor: pointer;

}
.btn:active{
    border: 4px solid black;
    height: 48px;
    width: 300px;
}