.app{
    background-color:#020230 ;
    height: 100vh;
    
    display: flex;
    width: 100%;
    flex-direction: row;
    flex-direction: row;
    color: aliceblue;
}
.main{
    width: 75%;
    
    background: url('./assets/background.jpg') center, linear-gradient(to top, rgba(0,0,0,0),#020230);
    
    display: flex;
    background-repeat: no-repeat;
    flex-direction: column;
    background-position: center;
    background-size: cover;
    
}
.top{
    position: relative;
    height: 50%;
    display: flex;
    flex-direction: column;
    
}
.bottom{
    height: 50%;
}
.timer{
    
    align-items: center;
    display: flex;
    height: 70px;
    padding: 5px;
    border-radius: 50%;
    width: 70px;
    justify-content: center;
    border: 5px solid yellow;
    font-size: 30px; 
    color: rgb(237, 252, 21);
    font-weight: 900;
    position: absolute;
    left: 80px;
    bottom: 10px;
}


.money{
    width: 20%;
    display: flex;
    align-items: center;
    justify-content: center;

}
.moneylist{
    list-style: none;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 5px;
    align-items: center;
}
.moneylistitem{
    padding: 5px;
    
    display: flex;
}
.moneylistitem.active{
background-color: yellow;
color: #020230;
border: 2px;
border-radius: 10px;
padding: 5px;
    
display: flex;
}
.number{
    margin-right: 5px;
    width: 30%;
}
.earned{
    position: relative;
    top:0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    display:flex;
    justify-content: center;
    align-items: center;
}